// UTILITY HELPERS

export const roundNum = (num) => Math.round(num)
export const ceilNum = (num) => Math.ceil(num)
export const floorNum = (num) => Math.floor(num)
export const truncNum = (num) => Math.trunc(num)

export const stringify = (string) => (string).toString()
export const reducer = (accumulator, currentValue) => accumulator + currentValue